.colorBox{
    width:2cm;
    height:1.5cm;
    border: thin solid grey;
    border-radius: 6px;
}

.Home{
    width:100vw;
    height:95vh;
}

.wheelDiv{
    padding: 1cm 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 1cm;
}

.comp{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 0.5cm;
    flex-wrap: wrap;
    row-gap: 0.5cm;
}

.heading{
    font-size: 25px;
    font-weight: bolder;
    text-shadow: 2px 4px 15px rgb(75, 0, 133);
    color:rgb(251, 251, 251);
    background-color: rgba(249, 157, 243, 0.523);
}
.colorHex{
    padding: 0.2cm 0.5cm;
}

.button{
    width:2cm;
    height:1.5cm;
    padding:0.2cm;
    background-color: rgb(229, 202, 246);
    border:none;
    border-radius: 6px;
    box-shadow: 3px 3px 2px rgb(195, 195, 195);
}
